#button {
  cursor: pointer;
  background: transparent;
  border: none;
  height: 56px;
  min-width: 40px;
  height: 16px;
  border-radius: 2px;
  width: auto;
  margin: 4px auto;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateY(0);
  transition: all 0.4s ease; }
  #button samp {
    border: solid;
    border-color: #eb2d73;
    position: absolute;
    width: 23px;
    height: 10px;
    transition: all 0.2s ease-in-out 0.2s; }
    #button samp:nth-child(4) {
      top: 0;
      right: 0;
      border-width: 2px 2px 0 0; }
    #button samp:nth-child(5) {
      bottom: 0;
      right: 0;
      border-width: 0px 2px 2px 0px; }
    #button samp:nth-child(2) {
      top: 0;
      left: 0;
      border-width: 2px 0px 0px 2px; }
    #button samp:nth-child(3) {
      bottom: 0;
      left: 0;
      border-width: 0px 0px 2px 2px; }
  #button .children {
    z-index: +2;
    font-size: 12px;
    transition: all 0.1s linear 0.6s;
    opacity: 0; }
  #button:focus {
    transition: all 0.1s ease;
    transform: translateY(0) scale(1); }
  #button.active {
    text-align: center;
    transition: all 0.2s ease-in-out 0.2s;
    color: #fff;
    background: #eb2d73;
    min-width: 100%;
    width: 100%;
    height: 48px;
    transform: translateY(-8px) scale(1); }
    #button.active .children {
      color: #eb2d73;
      color: #fff;
      opacity: 1;
      transition: all 0.1s linear 0.2s; }
    #button.active samp {
      transition: all 0.2s ease;
      width: 10px;
      height: 10px; }

