#input .field {
  width: 100%;
  height: 56px;
  border-radius: 4px;
  position: relative;
  margin: 32px 0;
  border-bottom: 1px solid rgba(235, 233, 215, 0.1);
  transition: 0.8s background-color cubic-bezier(0.68, -0.55, 0.265, 1.55), 0.8s transform cubic-bezier(0.68, -0.55, 0.265, 1.55); }
  #input .field ::-webkit-input-placeholder {
    color: transparent; }
  #input .field ::-ms-input-placeholder {
    color: transparent; }
  #input .field ::placeholder {
    color: transparent; }
  #input .field.active {
    z-index: +20; }
    #input .field.active input {
      padding: 24px 16px 8px 32px; }
    #input .field.active ::-webkit-input-placeholder {
      color: transparent; }
    #input .field.active ::-ms-input-placeholder {
      color: transparent; }
    #input .field.active ::placeholder {
      color: transparent; }
    #input .field.active .preamble {
      width: 100%; }
    #input .field.active label {
      top: 4px;
      opacity: 1;
      color: #eb2d73; }
  #input .field.locked {
    pointer-events: none; }
  #input .field input {
    width: 100%;
    height: 56px;
    position: relative;
    padding: 0px 32px;
    border: none;
    border-radius: 4px;
    font-size: 12px;
    font-weight: 300;
    line-height: normal;
    background-color: transparent;
    color: #ebe9d7;
    outline: none;
    box-shadow: 0px 4px 20px 0px transparent;
    transition: 0.6s background-color cubic-bezier(0.68, -0.55, 0.265, 1.55), 0.6s box-shadow cubic-bezier(0.68, -0.55, 0.265, 1.55), 0.1s padding cubic-bezier(0.68, -0.55, 0.265, 1.55);
    -webkit-appearance: none; }
    #input .field input::-webkit-input-placeholder {
      color: rgba(255, 255, 255, 0.8); }
    #input .field input::-moz-placeholder {
      color: rgba(255, 255, 255, 0.8); }
    #input .field input:-ms-input-placeholder {
      color: rgba(255, 255, 255, 0.8); }
    #input .field input:-moz-placeholder {
      color: rgba(255, 255, 255, 0.8); }
  #input .field label {
    position: absolute;
    top: 24px;
    left: 32px;
    font-size: 12px;
    font-weight: 600;
    line-height: 24px;
    color: #fff;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s all cubic-bezier(0.68, -0.55, 0.265, 1.55); }
    #input .field label.error {
      color: #ec392f; }
  #input .field .predicted {
    position: absolute;
    top: 8px;
    left: 32px;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    color: #ebe9d7;
    opacity: 1;
    pointer-events: none; }

#input .preamble {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  margin: 0 auto;
  height: 1px;
  display: block;
  background: #eb2d73;
  width: 0px;
  transform-origin: center;
  transition: all 1s cubic-bezier(0.68, -0.55, 0.265, 1.55); }

#input .icon {
  position: absolute;
  left: 0;
  bottom: 13px; }

